@import url(https://fonts.googleapis.com/css?family=Rubik:400);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* custom */

img {
  display: block;
  width: 100%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  --primary: #f5414c;
  --secondary: #ffcb64;
  --tertiary: #c786bd;
  --light: #bfc7d5;
  --dark: #292d3e;
  --darkest: #25293a;

  --warning: #ffcb64;
  --green: #c3e07a;
  --error: #f84e6e;
  --opacity80: rgba(0, 0, 0, 0.8);

  --sans: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial;

  --shadow: 2px 2px 6px rgba(0, 0, 0, 0.5);

  --shadow-poison: 0px 0px 24px #35f14a;
  --shadow-heal: 0px 0px 24px #d90143;
  --shadow-mana: 0px 0px 24px #3265f3;
}

html {
  font-size: 100%;
} /*16px*/

p {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h5 {
  margin: 1rem 0 1.38rem;
  font-family: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial;
  font-family: var(--sans);
  font-weight: 400;
  line-height: 1.3;
}

h4 {
  font-family: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial;
  font-family: var(--sans);
  font-weight: 400;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text_small {
  font-size: 0.8rem;
}

body {
  font-family: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial;
  font-family: var(--sans);
  font-weight: 400;
  line-height: 1.75;
  letter-spacing: 0.36px;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #25293a;
  background-color: var(--darkest);
  color: #bfc7d5;
  color: var(--light);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: #f5414c;
  color: var(--primary);
}

b {
  font-weight: 700;
}

img {
  display: block;
  max-width: 100%;
}

button {
  letter-spacing: 0.36px;
  outline: none;
  border: 2px solid transparent;
  background-color: #f5414c;
  background-color: var(--primary);
  color: #bfc7d5;
  color: var(--light);
  padding: 0 0.4rem;
  text-transform: uppercase;
  font-size: 0.7rem;
  width: 120px;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

button:hover {
  cursor: pointer;
  border: 2px solid #bfc7d5;
  border: 2px solid var(--light);
}

button:disabled {
  background-color: #2e2e2e;
  color: #6d6d6d;
  cursor: not-allowed;
}

button:disabled:hover {
  border: 2px solid transparent;
}

.width100 {
  width: 100%;
}

.center-center {
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.flex-1 {
  flex: 1 1;
}

.flex-justify-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.relative {
  position: relative;
}
.text-center {
  text-align: center;
}

.grid-container {
  display: flex;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  flex-wrap: wrap;
}

.color-error {
  color: #f84e6e;
  color: var(--error);
}

.color-warning {
  color: #ffcb64;
  color: var(--warning);
}

.filter-grayscale {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  justify-content: center;
  margin-top: 2rem;
}

.card-grid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 1px;
  gap: 1px;
}

.card {
  position: relative;
  border: 1px solid var(--warning);
  padding: 10px;
}

.card-image img {
  width: 100px;
  height: 100px;
}

.card-content {
  display: block;

  padding: 0.5rem;
  width: 100px;
  height: 150px;
  text-transform: uppercase;
  /* FIXME: */
  display: flex;
  flex-direction: column;
  text-align: left;
}

.card-content span {
  /* FIXME: linha abaixo do texto */
  font-size: 0.65rem;
  padding: 0.15rem 0;
  width: 100%;
}

.price {
  position: absolute;
  top: -12px;
  left: -6px;
  background: var(--secondary);
  color: var(--dark);
  padding: 4px;
  rotate: -28deg;
  font-size: 0.75rem;
  line-height: 1;
  font-weight: 700;
  box-shadow: var(--shadow);
}

.modal-container {
  background-color: var(--opacity80);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  opacity: 0;
  display: none;
}

.modal-container.active {
  -webkit-animation: fadeIn 0.1s forwards;
          animation: fadeIn 0.1s forwards;
  -webkit-animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
          animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  display: flex;
}

.modal {
  position: fixed;
  bottom: 0;
  left: 0;
  background: var(--dark);
  width: 100%;
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
  opacity: 0;
  padding: 2rem;
  display: none;
  border-top: 2px solid var(--primary);
  overflow-y: scroll;
  min-height: 366px;
  max-height: 90vh;
}

.modal.active {
  -webkit-animation: showUp 0.2s forwards;
          animation: showUp 0.2s forwards;
  -webkit-animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
          animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  display: block;
}
