@-webkit-keyframes showUp {
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes showUp {
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.game-container {
  margin-bottom: 2rem;
}

.game-menu {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.game-menu-buttons {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1px;
  gap: 1px;
  justify-content: center;
}

.game-fight,
.game-fight-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}

.game-fight {
  grid-gap: 1.8rem;
  gap: 1.8rem;
}

.game-fight-menu {
  margin-top: 1rem;
  grid-gap: 1px;
  gap: 1px;
}

.game-fight-log {
  overflow-y: auto;
  background: var(--dark);
  font-family: monospace;
  margin-top: 1rem;
  height: 100px;
  display: block;
  border: 2px solid var(--darkest);
}

.game-fight-log p {
  border-bottom: 2px solid var(--dark);
  margin-left: 10px;
  font-size: 0.75rem;
  margin-bottom: 0;
}

.menu-select {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
  grid-gap: 1px;
  gap: 1px;
}

.message {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 300;
  width: 100%;
  padding: 1rem;
  display: none;
  -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
  opacity: 0;
}

.message.success {
  color: var(--dark);
  background: var(--light);
}

.message.warning {
  color: var(--dark);
  background: var(--light);
}

.message.error {
  color: var(--dark);
  background: var(--light);
}

.message.success,
.message.warning,
.message.error {
  -webkit-animation: showUp 0.2s forwards;
          animation: showUp 0.2s forwards;
  -webkit-animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
          animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  display: block;
}

@-webkit-keyframes showUp {
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes showUp {
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.level-up {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
  opacity: 0;
}

.level-up.active {
  -webkit-animation: levelUp 0.6s forwards;
          animation: levelUp 0.6s forwards;
  -webkit-animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
          animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  top: 0px;
}

.level-up span {
  background: var(--secondary);
  color: var(--dark);
  padding: 1rem;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  width: 200px;
  justify-content: center;
}

@-webkit-keyframes levelUp {
  0% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes levelUp {
  0% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

.dices {
  display: flex;
  justify-content: center;
  grid-gap: 2px;
  gap: 2px;
  padding: 4px;
}

.dice {
  background: var(--dark);
  width: 50px;
  height: 50px;
  border-radius: 10px;
  display: grid;
  justify-content: center;
  align-content: center;
}

.dice.active {
  border: 2px solid var(--primary);
  color: var(--primary);
}

.position {
  margin: 0 10px;
}

.position.active {
  background-color: var(--dark);
  color: var(--green);
}

.position.pass {
  opacity: 0.25;
}

.hero {
  border: 2px solid var(--dark);
  padding: 1rem;
}

.hero-inline-container {
  display: flex;
  background: var(--secondary);
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.hero-inline {
  align-items: center;
  background: var(--secondary);
  color: var(--dark);
  display: grid;
  flex: 1 1;
  grid-gap: 4px;
  gap: 4px;
  grid-template-columns: repeat(7, 1fr);
  justify-content: space-between;
  left: 0;
  padding: 0 0.75rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.hero-inline span {
  font-size: 0.85rem;
  line-height: 1.4;
}

